.history-page {
  overflow-x: auto;
}
.history-page h2,
h4 {
  text-align: center;
  margin: 20px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}
.history-page table {
  margin: auto;
  width: 100%;
}
.history-page th,
td {
  text-align: center;
  padding: 10px;
  text-transform: capitalize;
}
.history-page table a {
  color: #03a5ce;
}

.history-page img {
  width: 70px;
  height: 100px;
  object-fit: cover;
}
